import React, { useState } from "react";
import styled from "styled-components";
import { TextField, Button, Typography, Box } from "@mui/material";

// Estilos principais
const SimuladorContainer = styled.div`
  font-family: "Montserrat", sans-serif;
  background: linear-gradient(180deg, #011e3b, #343a40);
  color: #ffffff;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
`;

// Funo para calcular o rendimento bruto
const calcularResgateBruto = (valorInvestido, taxaDebenture, taxaIPCA, meses) => {
  const taxaTotal = taxaDebenture + taxaIPCA; // Rentabilidade total
  return valorInvestido * Math.pow(1 + taxaTotal / 100, meses / 12);
};

// Funo para calcular o rendimento lquido
const calcularResgateLiquido = (
  resgateBruto,
  impostoRenda,
  taxaCorretora,
  valorInvestido
) => {
  const descontoIR = resgateBruto * (impostoRenda / 100);
  const descontoCorretora = valorInvestido * (taxaCorretora / 100);
  return resgateBruto - descontoIR - descontoCorretora;
};

// Funo para calcular rentabilidade lquida
const calcularRentabilidade = (resgateLiquido, valorInvestido, meses) => {
  const lucroLiquido = resgateLiquido - valorInvestido;
  const rentabilidadeAA = ((resgateLiquido / valorInvestido) ** (12 / meses) - 1) * 100;
  const rentabilidadeAM = ((resgateLiquido / valorInvestido) ** (1 / meses) - 1) * 100;
  return { lucroLiquido, rentabilidadeAA, rentabilidadeAM };
};

const SimuladorDebentures = () => {
  const [dataCompra, setDataCompra] = useState("");
  const [dataVenda, setDataVenda] = useState("");
  const [valorInvestido, setValorInvestido] = useState(1000);
  const [taxaDebenture, setTaxaDebenture] = useState(5);
  const [impostoRenda, setImpostoRenda] = useState(0);
  const [taxaIPCA, setTaxaIPCA] = useState(3);
  const [taxaCorretora, setTaxaCorretora] = useState(0);
  const [taxaTesouroIPCA, setTaxaTesouroIPCA] = useState(6);
  const [resultado, setResultado] = useState(null);

  // Calcula a diferena em meses entre a data de compra e a data de venda
  const calcularMeses = (compra, venda) => {
    const data1 = new Date(compra);
    const data2 = new Date(venda);
    return (data2.getFullYear() - data1.getFullYear()) * 12 + (data2.getMonth() - data1.getMonth());
  };

  const handleCalcular = () => {
    const meses = calcularMeses(dataCompra, dataVenda);
    if (meses <= 0) {
      alert("A data de venda deve ser posterior  data de compra.");
      return;
    }

    const resgateBruto = calcularResgateBruto(valorInvestido, taxaDebenture, taxaIPCA, meses);
    const resgateLiquido = calcularResgateLiquido(resgateBruto, impostoRenda, taxaCorretora, valorInvestido);
    const { lucroLiquido, rentabilidadeAA, rentabilidadeAM } = calcularRentabilidade(resgateLiquido, valorInvestido, meses);

    setResultado({
      resgateBruto,
      resgateLiquido,
      lucroLiquido,
      rentabilidadeAA,
      rentabilidadeAM,
    });
  };

  return (
    <SimuladorContainer>
      <Typography variant="h4" align="center" gutterBottom>
        Simulador de Debntures
      </Typography>
      <Box display="flex" flexDirection="column" gap={2} marginBottom={4}>
        <TextField
          label="Data de Compra"
          type="date"
          InputLabelProps={{ shrink: true }}
          value={dataCompra}
          onChange={(e) => setDataCompra(e.target.value)}
          variant="outlined"
        />
        <TextField
          label="Data de Venda"
          type="date"
          InputLabelProps={{ shrink: true }}
          value={dataVenda}
          onChange={(e) => setDataVenda(e.target.value)}
          variant="outlined"
        />
        <TextField
          label="Valor Investido (R$)"
          type="number"
          value={valorInvestido}
          onChange={(e) => setValorInvestido(Number(e.target.value))}
          variant="outlined"
        />
        <TextField
          label="Taxa da Debnture (%)"
          type="number"
          value={taxaDebenture}
          onChange={(e) => setTaxaDebenture(Number(e.target.value))}
          variant="outlined"
        />
        <TextField
          label="Imposto de Renda (%)"
          type="number"
          value={impostoRenda}
          onChange={(e) => setImpostoRenda(Number(e.target.value))}
          variant="outlined"
        />
        <TextField
          label="Taxa IPCA (%)"
          type="number"
          value={taxaIPCA}
          onChange={(e) => setTaxaIPCA(Number(e.target.value))}
          variant="outlined"
        />
        <TextField
          label="Taxa Corretora (%)"
          type="number"
          value={taxaCorretora}
          onChange={(e) => setTaxaCorretora(Number(e.target.value))}
          variant="outlined"
        />
        <TextField
          label="Taxa Tesouro IPCA (%)"
          type="number"
          value={taxaTesouroIPCA}
          onChange={(e) => setTaxaTesouroIPCA(Number(e.target.value))}
          variant="outlined"
        />
      </Box>

      <Button variant="contained" color="primary" onClick={handleCalcular}>
        Calcular
      </Button>

      {resultado && (
        <Box marginTop={4}>
          <Typography variant="h6">Resultado do Simulador:</Typography>
          <Typography>Resgate Bruto: R$ {resultado.resgateBruto.toFixed(2)}</Typography>
          <Typography>Resgate Lquido: R$ {resultado.resgateLiquido.toFixed(2)}</Typography>
          <Typography>Lucro Lquido: R$ {resultado.lucroLiquido.toFixed(2)}</Typography>
          <Typography>Rentabilidade a.a.: {resultado.rentabilidadeAA.toFixed(2)}%</Typography>
          <Typography>Rentabilidade a.m.: {resultado.rentabilidadeAM.toFixed(2)}%</Typography>
        </Box>
      )}
    </SimuladorContainer>
  );
};

export default SimuladorDebentures;

